import { createElement } from "./elements.js";

//–ú–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å—Å—è

// –°–æ–∑–¥–∞–µ—Ç –æ–∫–Ω–æ –∂–∞–ª–æ–±—ã
function createComplaintWindow(parent) {
  const rootDiv = document.createElement("div");
  rootDiv.className = "modal";
  parent.append(rootDiv);

  const body = createElement({
    tag: "div",
    className: ["modal_body"],
    place: rootDiv,
  });

  const content = createElement({
    tag: "div",
    className: ["modal_content"],
    place: body,
  });

  const title = createElement({
    tag: "div",
    className: ["modal_title"],
    text: "–ñ–∞–ª–æ–±–∞ –Ω–∞ –ø–∏–Ω",
    place: content,
  });

  //—á–µ–∫–±–æ–∫—Å—ã —Å –æ–±–µ—Ä—Ç–∫–æ–π
  const text = createElement({
    tag: "div",
    className: ["modal_text"],
    place: content,
  });

  const arrText = [
    "–°–ø–∞–º",
    "–ß–ª–µ–Ω–æ–≤—Ä–µ–¥–∏—Ç–µ–ª—å—Å—Ç–≤–æ",
    "–õ–æ–∂–Ω–∞—è –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è",
    "–ê–≥—Ä–µ—Å—Å–∏–≤–Ω—ã–µ –¥–µ–π—Å—Ç–≤–∏—è",
    "–û–ø–∞—Å–Ω—ã–µ —Ç–æ–≤–∞—Ä—ã",
    "–ü—Ä–µ—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ –∏–ª–∏ –∫—Ä–∏—Ç–∏–∫–∞",
    "–°—Ü–µ–Ω—ã –Ω–∞—Å–∏–ª–∏—è",
    "–ù–∞—Ä—É—à–µ–Ω–∏–µ –∫–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ—Å—Ç–∏",
  ];
  for (let i = 0; i <= arrText.length - 1; i++) {
    const label = createElement({
      tag: "label",
      className: ["modal_label"],
      text: arrText[i],
      place: text,
    });

    createElement({
      tag: "input",
      className: ["modal_checkbox"],
      attribute: {
        type: "checkbox",
      },
      place: label,
    });

    createElement({
      tag: "span",
      className: ["modal_span"],
      place: label,
    });
  }

  const footer = createElement({
    tag: "div",
    className: ["modal_footer"],
    place: content,
  });

  const buttonClose = createElement({
    tag: "button",
    className: ["modal_close"],
    text: "–û—Ç–º–µ–Ω–∞",
    place: footer,
  });

  const buttonSubmit = createElement({
    tag: "button",
    className: ["modal_submit"],
    text: "–û—Ç–ø—Ä–∞–≤–∏—Ç—å",
    place: footer,
  });
  const buttonSubmitSpinner = createElement({
    tag: "span",
    className: ["spinner"],
    place: buttonSubmit,
  });

  // –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É "–æ—Ç–º–µ–Ω–∞", –∑–∞–∫—Ä—ã—Ç—å –∏ —É–¥–∞–ª–∏—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ
  buttonClose.addEventListener("click", () => {
    rootDiv.remove();
  });

  // –ü—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ –∫–Ω–æ–ø–∫—É"–æ—Ç–ø—Ä–∞–≤–∏—Ç—å",—Å—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç —Å–ø–∏–Ω–Ω–µ—Ä +
  // –∑–∞–∫—Ä—ã–≤–∞–µ—Ç—Å—è –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ + —Å—Ä–∞–±–∞—Ç—ã–≤–∞–µ—Ç alert –¥–ª—è —ç–∫—Ä–∞–Ω–æ–≤ >=1024
  buttonSubmit.addEventListener("click", () => {
    buttonSubmit.style.display = "block";
    buttonSubmitSpinner.style.display = "flex";
    buttonSubmitSpinner.classList.add("spin");
    setTimeout(() => {
      buttonSubmitSpinner.style.display = "none";
      rootDiv.remove();

      setTimeout(() => {
        if (window.innerWidth >= 1024) {
          alert("–í–∞—à–∞ –∂–∞–ª–æ–±–∞ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞!");
        }
      }, 700);
    }, 1500);
  });

  //–û–∫—Ä–∞—Å–∫–∞ —Ç–µ–∫—Å—Ç–∞ –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —á–µ–∫–±–æ–∫—Å
  const selectors = rootDiv.querySelectorAll(".modal_label");
  for (const selector of selectors) {
    selector.addEventListener("click", (e) => {
      const container = e.currentTarget;
      if (container.firstElementChild.checked) {
        container.style.color = "red";
      } else {
        container.style.color = "black";
      }
    });
  }
}

export { createComplaintWindow };
